import React from 'react';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import StoreMallDirectoryIcon from '@material-ui/icons/StoreMallDirectory';
import {NavLink} from 'react-router-dom'
import {Navbar, Container} from 'react-bootstrap'



const TheNavbar = ({totalItems}) => {

  

    return ( 
        <div>
           <Navbar bg="navbar fixed-top" expand="lg">
           <Container>
  <Navbar.Brand><StoreMallDirectoryIcon style={{fontSize: "30px"}} /></Navbar.Brand>
    <div className="ml-auto row nav_items">
      <h4> <NavLink  exact to="/" activeClassName="active">Shop</NavLink></h4>
      <span> <NavLink   exact to="/cart" activeClassName="active"><AddShoppingCartIcon style={{fontSize: 35}}  />  </NavLink></span>
       <p className="totalItems">{totalItems}</p>
      
       
    </div>
    </Container>
</Navbar>
);
        </div>
     );
}
 
export default TheNavbar;