
const CartItem = ({item, handleRemoveFromCart, handleUpdateCartQty}) => {
    return ( 
        <div className="cartItem">
        <div className="item">
            <img alt="img" src={item.media.source} /> 
            <h4>{item.name}</h4>
            <h5>{item.line_total.formatted_with_symbol}</h5>
        </div>
            <div className="buttons">
                <button onClick={() => handleUpdateCartQty(item.id, item.quantity + 1 )} className="btn">+</button>
                <h4>{item.quantity}</h4>
                <button onClick={() => handleUpdateCartQty(item.id, item.quantity - 1 )} className="btn">-</button> 
                <button onClick={() => handleRemoveFromCart(item.id)} className="btn remove">remove</button>

            </div>
           

        </div>
     );
}
 
export default CartItem;