
import React from 'react';
import Cart from '../../components/Cart/Cart'
const TheCart = ({cart, handleUpdateCartQty, handleRemoveFromCart, handleEmptyCart }) => {

 


    return (
        <div>
                
                <Cart handleUpdateCartQty={handleUpdateCartQty} handleEmptyCart={handleEmptyCart} handleRemoveFromCart={handleRemoveFromCart} cart={cart} />
        </div>
      );
}
 
export default TheCart;