import  Shop from '../src/pages/shop/index'
import TheCart from '../src/pages/cart/index'
import TheNavbar from './components/Navbar'
import {BrowserRouter, Route, Switch} from 'react-router-dom'
import React, {useEffect, useState} from 'react';
import {commerce} from './lib/commerce'
const App = () => {


        const [products, setProducts] = useState({});
        const [cart, setCart] = useState({});
        
      
      
        const fetchProducts = async () => {
          const {data} = await commerce.products.list();
      
          setProducts(data);
        }

      
        const fetchCart = async () => {
          
          setCart(await commerce.cart.retrieve());
        }
      
        const handleAddToCart = async (productId, quantity) => {
          const item = await commerce.cart.add(productId, quantity);
          setCart(item.cart)
        }

        const handleUpdateCartQty = async (productId, quantity) => {
            const {cart} = await commerce.cart.update(productId, {quantity});
        
            setCart(cart)
          }
        const handleRemoveFromCart = async (productId) => {
            const {cart} = await commerce.cart.remove(productId);
            setCart(cart)
        }
        
        const handleEmptyCart = async () => {
         const {cart} = await commerce.cart.empty();
           setCart(cart)
        
        } 
     
       
      
        useEffect(() => {
            fetchProducts();
           
            fetchCart();
        }, [])
    return (
        <div>
        <BrowserRouter>
        <TheNavbar totalItems={cart.total_items}/>
        <Switch>
        
        
        <Route exact path="/">
                <Shop handleAddToCart={handleAddToCart} cart={cart} products={products}  />
        </Route>
        <Route exact path="/cart">
                <TheCart cart={cart} handleUpdateCartQty={handleUpdateCartQty} handleRemoveFromCart={handleRemoveFromCart} handleEmptyCart={handleEmptyCart} />
       </Route>

     

       

        </Switch>

        </BrowserRouter>
        </div>
    )
}

export default App
