import React from 'react';
import Products from '../../components/produects/Products'

const Shop = ({handleAddToCart, cart, products}) => {



  return ( 

    <div>
    
      <div className="welcome"> 
         <div className="overlay">
         <h1>welcome to the <span>store</span></h1>
         </div>
      </div>
     <Products   handleAddToCart={handleAddToCart} products={products}  />
      
    </div>

   );
}
 
export default Shop;