import CartItem from './CartItem';
import StripeCheckout from 'react-stripe-checkout'

const Cart = ({cart, handleUpdateCartQty, handleEmptyCart, handleRemoveFromCart}) => {

const handleToken = () => {
   handleEmptyCart()
}



    const FilledCart = () => {
        return (
            <>
      <div className="cart">
        {cart.line_items.map((item) => (
          <div key={item.id}>
           <CartItem handleUpdateCartQty={handleUpdateCartQty} handleRemoveFromCart={handleRemoveFromCart} item={item} />
          </div>
        ))}
      </div>
      <div className="check">
        <h1>Subtotal: {cart.subtotal.formatted_with_symbol}</h1>
        <div>
          <button onClick={handleEmptyCart} className="btn btn-danger">Empty cart</button>

         
          <StripeCheckout token={handleToken} stripeKey="pk_test_51IZHd1AAA3qxqqI6jR7QS2WDsQUJvKLc3ah7qYvB8jgpHUJIWzALw5kdn4yeNSdbCgOe1OQwSb18HhKIyLT3RIc0004tCU9RyL" />
    
          
       
        </div>
      </div>
    </>
        )
    }

    
    if(!cart.line_items) return <h1 className="loading">loading...</h1>;
    return ( 
        <div className="theCart">
                <h1>your Shopping cart</h1>
            {FilledCart()}
        </div>
     );
}
 
export default Cart;